import React, { Component } from 'react';
import './App.css';
import { StoryView } from './StoryView';
import { createStory } from './Story';
import { LandingView } from './LandingView';

import "@ui5/webcomponents/dist/Toolbar";
import "@ui5/webcomponents/dist/Title";
import '@ui5/webcomponents/dist/ToggleButton'

/*global inkjs*/

class App extends Component {
  constructor() {
    super();
    this.state = {
      inkStory: null,
      story: null,
      initialLoad: true,
    }
  }

  doContinue() {
    this.state.inkStory.Continue();
    this.setState({
      ...this.state,
      story: createStory(this.state.inkStory)
    })
  }

  choiceSelected(i) {
    this.state.inkStory.ChooseChoiceIndex(i);
    this.state.inkStory.Continue();
    this.setState({
      ...this.state,
      story: createStory(this.state.inkStory)
    });
  }

  async begin() {
    const response = await fetch("locadeserta.ink.json");
    const json = await response.text();
    const inkStory = new inkjs.Story(json);
    inkStory.Continue();
    this.setState({
      ...this.state,
      inkStory,
      story: createStory(inkStory),
      initialLoad: false
    });
  }

  render() {
    const { story, inkStory } = this.state;
    return (
      <div>
       <ui5-toolbar id="nav">
            <ui5-togglebutton onPress={() => console.log('lol')} id="menu-btn" icon="sap-icon://menu" type="Transparent"></ui5-togglebutton>
            <ui5-title level="H5">UI5 webcomponents</ui5-title>
        </ui5-toolbar>
        {
          this.state.initialLoad && <LandingView onStart={() => this.begin()} />
        }
        {
          inkStory !== null && <StoryView story={story}
            onNext={() => this.doContinue()}
            onChoiceSelected={(i) => {
              this.choiceSelected(i)
            }} />
        }
      </div>
    );
  }
}

export default App;
